<script>
import poco from '../../contact/domain.js'
import axios from 'axios'

export default {
  name: "LeftSidebar",
  props: {},
  data() {
    return {
      Categories:[]
    }
  },
  mounted() {
    var self = this
    axios.get(`${poco.domain}/categories`)
    .then(res => {
      self.Categories = JSON.parse(JSON.stringify(res.data))
    })
    .catch(err => {
      console.log(err)
    })
  }
};
</script>

